header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #ffffff;
  border-top: 1px solid #dddddd;
}

header div {
  display: flex;
  justify-content: space-between;
  padding: 20px 50px;
}

header h1 {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

header a {
  text-decoration: none;
  color: #333333;
}

header button {
  display: inline-block;
  padding: 5px 18px;
  margin-left: auto;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  cursor: pointer;
}