.cart {
  padding: 70px 20px;
}

.cart h2 {
  margin: 0 0 0 20px;
  font-size: 24px;
  font-weight: bold;
  color: #333333;
}

.cart ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr); 
  padding: 0; 
}

@media (max-width: 768px) {
  .cart ul {
    grid-template-columns: repeat(2, 1fr);
    padding: 0;
  }
}

.cart ul li {
  list-style: none;
  font-size: 16px;
  line-height: 2;
  margin: 10px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.cart ul li button {
  margin: 10px;
}