.bottom-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  border-top: 1px solid #dddddd;
}

.bottom-bar img {
  width: 30px;
  height: 30px;
  padding: 10px;
}